.soundcloud-player {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
    padding: 20px;
    background-color: #F9F9F9;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0,0,0,0.1);
  }
  
  .track-info {
    flex-grow: 1;
    text-align: center;
    margin-right: 20px;
  }
  
  .track-title {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 5px;
  }
  
  .controls {
    align-items: center;
    justify-content: center;
  }
  
  .controls button {
    background-color: transparent;
    border: none;
    outline: none;
    cursor: pointer;
    font-size: 18px;
    color: #333;
  }
  
  .controls button:hover {
    color: #999;
  }

.slider{
    -webkit-appearance: none;
    width: 100%;
    height: 15px;
    border-radius: 5px;
    background: #d3d3d3;
    outline: none;
    opacity: 0.7;
    -webkit-transition: .2s;
    transition: opacity .2s;
    cursor: pointer;
}

.slider:hover{
    opacity: 1;
}

.slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background: #4CAF50;
    cursor: pointer;
}

.slider::-moz-range-thumb {
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background: #4CAF50;
    cursor: pointer;
}

.album-cover{
    width: 100px;
    height: 100px;
    border-radius: 10px;
    margin-right: 20px;
}